.body__picture-leaf {
  position: absolute;
  top: 1254px;
  left: 0;
  z-index: -3;

  &::before {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, transparent 10%, rgba(255, 255, 255, 0.4) 80%);
    z-index: 1;
    backdrop-filter: blur(1px);
  }
}

